import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import Head from 'next/head';
import { ThemeProvider } from 'styled-components';
import Sticky from 'react-stickynode';
import { NewOrderTheme } from '../theme/neworder';
import {
  GlobalStyle,
  NewOrderWrapper,
  ConditionWrapper,
} from '../containers/NewOrder/app.style';
import { ResetCSS } from '../static/css/style';
import Link from 'next/link';
import Navbar from '../containers/NewOrder/Navbar';
import DomainSection from '../containers/NewOrder/Banner';
import FeatureSection from '../containers/NewOrder/FeatureSection';
import PriceSection from '../containers/NewOrder/priceSection';
import PhoneSection from '../containers/NewOrder/phoneSection';
import ControllSection from '../containers/NewOrder/Control';
import TestimonialSection from '../containers/NewOrder/Testimonial';
import PartnerHistory from '../containers/NewOrder/PartnerHistory';
import Button from 'reusecore/src/elements/Button';
import PaymentSection from '../containers/NewOrder/PaymentSection';
import Footer from '../containers/NewOrder/Footer';
import FeatureSlider from '../containers/NewOrder/FeatureSlider';
import FeatureSliderTwo from '../containers/NewOrder/FeatureSliderTwo';
import 'react-image-gallery/styles/css/image-gallery.css';
import { DrawerProvider } from '../contexts/DrawerContext';
import { ButtonWrapper } from '../containers/NewOrder/Banner/banner.style';
function getSize() {
  return {
    innerHeight: window.innerHeight,
    innerWidth: window.innerWidth,
    outerHeight: window.outerHeight,
    outerWidth: window.outerWidth,
  };
}

function useWindowSize() {
  let [windowSize, setWindowSize] = useState(getSize());

  function handleResize() {
    setWindowSize(getSize());
  }

  useEffect(() => {
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return windowSize;
}

export default () => {
  const size = process.browser && useWindowSize();
  return (
    <ThemeProvider theme={NewOrderTheme}>
      <>
        <Head>
          <title>Calloney - Logiciel de reservations téléphoniques</title>
          <meta name="Description" content="React next landing page" />
          <meta name="theme-color" content="#ec5555" />
          {/* Flaticon css */}
          <link rel="stylesheet" href="../static/css/flaticon.css" />
          <link
            href="https://fonts.googleapis.com/css?family=Open+Sans:300,400,700"
            rel="stylesheet"
          />
        </Head>

        <ResetCSS />
        <GlobalStyle />

        <NewOrderWrapper>
          <Sticky top={0} innerZ={9999} activeClass="sticky-nav-active">
            <DrawerProvider>
              <Navbar />
            </DrawerProvider>
          </Sticky>
          <DomainSection />
          <FeatureSection />
          <PriceSection />
          <PhoneSection/>
          <ButtonWrapper>
          <Link href="#services">
                <a>
                  <Button 
                  style={{
                   title: 'Premiers pas avec Calloney ',
                   type: 'button',
                   fontSize: '14px',
                   fontWeight: '600',
                   color: '#fff',
                   borderRadius: '4px',
                   pl: '22px',
                   pr: '22px',
                   colors: 'primaryWithBg',
                   minWidth: '156px',
                   fontSize: '14px',
                   fontWeight: '500',
                   }} />
                </a>
              </Link>
            </ButtonWrapper>
          {/* <ControllSection />
          <ConditionWrapper id="keyfeature">
            {size.innerWidth > 1100 ? <FeatureSlider /> : <FeatureSliderTwo />}
          </ConditionWrapper>
          <PartnerHistory />
          <PaymentSection />
          <TestimonialSection />
          <div
            style={{
              width: 38,
              height: 100,
              position: 'fixed',
              zIndex: 99991,
              right: 0,
              top: 'calc(50% - 55px)',
              cursor: 'pointer',
            }}
          >
            <ButtonWrapper1>
              <span href="/login"> Pas Premiers</span>
            </ButtonWrapper1>
          </div> */}
          {/* <Footer /> */}
        </NewOrderWrapper>
      </>
    </ThemeProvider>
  );
};
